body {
  box-sizing: border-box;
  margin: 0;
}

.container {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  margin: 0 auto;
  position: relative;
}

.stopwatch__center {
  z-index: 3;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.stopwatch__hand {
  transform-origin: bottom;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -100%)rotate(0);
}

.stopwatch__hand--minute {
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate-hand;
}

.stopwatch__hand--second {
  z-index: 2;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate-hand;
}

.stopwatch--speed-up .stopwatch__hand--minute {
  animation-duration: 600s;
}

.stopwatch--speed-up .stopwatch__hand--second {
  animation-duration: 10s;
}

@keyframes rotate-hand {
  to {
    transform: translate(-50%, -100%)rotate(360deg);
  }
}
